@use 'abstract' as *;
@use 'base/colors' as c;

.header {
    &__container {
        display: grid;
        justify-content: center;
        @include container ($width: 375px);
    }

    &-img {
        height: auto;
        margin-top: 2em;
    
        img {
            height: 330px;
            object-fit: cover;
            object-position: 22px 0;
        }
    }

    &__title,
    &__desc {
        text-align: center;
        @include container($width: calc(375px * 0.9));
        margin-top: 0.75em;
    }

    &__title {
        margin-top: 1em;
    }
    button {
        margin: 1.75em auto 5em;
    }
}

