@use 'abstract' as *;
@use 'base/colors' as c;

.nav {
  .nav__container {
    @include container($width: calc(375px * 0.9));
    display: flex;
    align-items: center;
    margin-top: 2em;
  }

  .nav__logo {
    margin-right: calc((375px * 0.9) - 150px);
  }

  .nav__icon {
    color: c.$gray;
    font-size: 30px;

  }

  .nav__links,
  .nav__sign-btn {
    display: none;
  }
}


