@use 'colors' as c;
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap');

/* font-style */
$font-family: 'Poppins', sans-serif;

/* font-weight */
$fw-regular: 500;
$fw-bold: 700;

body {
    font-family: $font-family;
    font-weight: $fw-regular;
}

.header {
    &__title {
        font-weight: $fw-bold;
        font-size: 40px;
        line-height: 1.125;
    }

    &__desc {
        font-size: 18px;
        line-height: 1.6;
        color: c.$grayishViolet;
    }
}














